
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Inter;
  font-size: 16px;
  text-rendering: optimizeLegibility;
  --webkit-font-smoothing: subpixel-antialiased;
  overflow-y: hidden;
}

div {
  position:relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.home {
  content : url('../public/images/home.svg');
}
.active > .home {
  content : url('../public/images/homeActive.svg');
}
.estadisticas {
  content : url('../public/images/estadisticas.svg');
}
.active > .estadisticas {
  content : url('../public/images/estadisticasActive.svg');
}
.perfil {
  content : url('../public/images/perfil.svg');
}
.active > .perfil {
  content : url('../public/images/perfilActivate.svg');
}
.img-option {
  content: url('../public/images/iunselected.svg');
}
